.cross-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: none;
  border: none;
  border-radius: 50%;
  padding: 0;

  width: 1em;
  height: 1em;

  color: rgba(white, 0.4);
  line-height: 1;

  cursor: pointer;

  transform: scale(0.85);
  transition: transform 0.15s ease-in-out;
  will-change: transform;

  user-select: none;

  &:hover {
    transform: scale(1);
    background-color: rgba(black, 0.2);
    opacity: 1;

    color: var(--danger-color);
  }

  &:focus {
    outline: none;
    box-shadow: 0 0 0 var(--focus-outline-width) white;
  }

  span {
    position: absolute;
  }
}
